.t-content-text {
    &--alt {
        font-size: 1.4rem;

        line-height: 1.8;

        h2 {
            margin: 0 0 35px 0;
            
            font-size: 2rem;
            font-weight: 400;

            line-height: 1.4;

            @include gridle_state(m) {
                margin-bottom: 15px;
            }
        }

        p {
            @extend %helper-clear-margins;

            margin: 20px 0;
        }
    }
}