.c-nav {
    &--main {
        @include gridle_state(t) {
            position: fixed;
            top: 0;
            left: 0;

            display: flex;
            justify-content: center;
            align-items: stretch;

            z-index: 800;

            width: 100%;
            height: 100%;

            opacity: 0;
            visibility: hidden;

            background: rgba($color_white, 0.8);

            transition: opacity 200ms ease-in-out, visibility 200ms ease-in-out;
        }

        &__list {
            @extend %helper-reset-list;
            @extend %display-flex;
            @extend %align-items-center;
            @extend %justify-content-flex-start;

            height: 80px;

            @include gridle_state(t) {
                height: auto;
                flex-direction: column;
                justify-content: center;
            }
        }

        &__item {
            margin-right: 30px;

            @include gridle_state(t) {
                margin-bottom: 5vh;
                margin-right: 0;
            }

            .c-label {
                font-size: 1.6rem;
                font-weight: 500;
                text-transform: uppercase;
            }

            &:hover,
            &.is-active {
                .c-label {
                    color: $color_red;
                }
            }
        }
    }
}