.c-nav {
    &--foot-links {
        &__list {
            @extend %helper-reset-list;
            @extend %display-flex;
            @extend %align-items-center;
            @extend %justify-content-flex-start;
        }

        &__item {
            position: relative;

            margin-right: 20px;

            .c-label {
                font-size: 1.2rem;
                font-weight: 400;
                text-transform: uppercase;

                line-height: 1;

                color: $color_gray_3;

                &:hover {
                    color: $color_white;
                }
            }
        }
    }
}