.b-box {
    &--logo-content {
        @extend %display-flex;
        @extend %align-items-stretch;
        @extend %justify-content-flex-start;

        color: $color_gray_2;
        background: $color_white;
        box-shadow: 0 0 60px rgba(0, 0, 0, 0.4);

        @include gridle_state(m-m) {
            display: block;
        }

        &__logo {
            width: 260px;

            background: $color_black;

            @include gridle_state(m-m) {
                width: 100%;
            }
        }

        &__logo,
        &__content {
            padding: 20px;
        }

        &__logo {
            .c-logo {
                display: block;

                margin-left: auto;
                margin-right: auto;
            }
        }
    }
}