.l-sec {
    &--default {
        padding: 50px 0;

        @include gridle_state(t) {
            padding: 30px 0;
        }

        & + .l-sec--default--bg {
            margin-top: 0;
        }

        &--bg {
            margin: 45px 0;
            background: $color_white;

            @include gridle_state(t) {
                margin: 30px 0;
            }
        }
    }
}