.c-list {
    &--houses {
        @extend %display-flex;
        @extend %align-items-flex-start;
        @extend %justify-content-flex-start;
        @extend %flex-wrap-wrap;

        margin: 0 -15px;

        &__item {
            width: 50%;
            
            padding: 0 15px;
            margin-bottom: 30px;

            @include gridle_state(m-s) {
                width: 100%;
                margin-bottom: 15px;
            }
        }
    }
}