.l-sec {
    &--foot {
        padding: 67px 0;

        color: $color_white;
        background: $color_black_2 url("../img/default/lines.png") no-repeat 50% 0;

        @include gridle_state(t) {
            padding: 30px 0;
        }

        &__row {
            &--nav {
                position: relative;

                padding-left: 390px;
                padding-top: 15px;

                @include gridle_state(t) {
                    padding-left: 120px;
                    padding-top: 0;
                }

                @include gridle_state(m-m) {
                    padding-left: 0;
                }

                .c-logo {
                    position: absolute;
                    top: 0;
                    left: 0;

                    @include gridle_state(m-m) {
                        position: relative;

                        margin-bottom: 30px;
                    }
                }
            }
        }

        &__author,
        &__copy {
            margin: 50px 0 0 0;

            font-size: 1.3rem;

            line-height: 1;

            color: $color_gray_4;

            @include gridle_state(t) {
                margin-top: 20px;
            }

            @include gridle_state(m) {
                text-align: center;
            }
        }

        &__author {
            text-align: right;

            margin-top: -13px;

            color: $color_gray_4;

            a {
                color: $color_gray_4;
            }

            @include gridle_state(m) {
                margin-top: 10px;
                text-align: center;
            }
        }

        .c-nav--foot-links__list {
            @include gridle_state(m) {
                justify-content: center;
            }
        }

        .c-list--social {
            margin: 46px 0 20px 0;

            @include gridle_state(t) {
                margin: 30px 0 20px 0;
            }

            @include gridle_state(m) {
                justify-content: center;
            }
        }
    }
}