.c-list {
    &--qualities {
        @extend %display-flex;
        @extend %align-items-stretch;
        @extend %justify-content-flex-start;
        @extend %flex-wrap-wrap;

        margin: 0;

        box-shadow: 0 0 60px rgba(0, 0, 0, 0.4);

        &__item {
            @extend %display-flex;
            @extend %align-items-stretch;
            @extend %justify-content-flex-start;
            
            flex-grow: 1;
            
            width: (100 / 3) * 1%;

            @include gridle_state(t-m) {
                width: 50%;
            }
            
            @include gridle_state(m-s) {
                width: 100%;
            }

            &:nth-child(2n+1) {
                .b-box--quality {
                    background: $color_red;
                }

                .b-box--quality__subtitle {
                    color: $color_black;
                }
                
                .b-box--quality__title span:after {
                    color: $color_white;
                }
            }
        }
    }
}