.c-btn {
    &--trigger-nav-main {
        display: block;

        position: relative;
        z-index: 850;

        width: 30px;
        height: 25px;

        font-size: 0;
        text-indent: -9999px;

        border: 0;
        appearance: none;
        box-shadow: none;
        background: none;
        overflow: hidden;

        margin: 0;
        padding: 0;

        cursor: pointer;

        &:focus {
            outline: none;
        }

        &.is-active {
            span {
                background: none;

                &:before {
                    top: 0;
                    transform: rotate(45deg);
                }

                &:after {
                    bottom: 0;
                    transform: rotate(-45deg);
                }
            }
        }

        & > span {
            display: block;
            position: absolute;
            top: 50%;
            width: 30px;
            height: 1px;
            border-radius: 50px;
            background: $color_red;

            transform: translateY(-50%);

            transition: 200ms ease-in-out;

            &:before, 
            &:after {
                position: absolute;
                display: block;
                left: 0;
                width: 100%;
                height: 1px;
                border-radius: 30px;
                background: $color_red;
                content: "";

                transition: 200ms ease-in-out;
            }

            &:before {
                top: -10px;
            }

            &:after {
                bottom: -10px;
            }
        }
    }
}