.t-head {
    &--main {
        @extend %helper-clear-margins;

        margin: 50px 0;

        font-family: $font_primary;
        font-size: 3.5rem;
        font-weight: 700;
        text-transform: uppercase;
        letter-spacing: 7px;

        line-height: 1;

        @include gridle_state(t) {
            margin: 30px 0;

            font-size: 2.4rem;
        }

        span {
            position: relative;

            &:after {
                position: absolute;
                top: 8px;
                right: -38px;

                content: '';

                width: 14px;
                height: 14px;

                background: url("../img/default/ico-plus-1-red.png") no-repeat;
                background-size: cover;

                @include gridle_state(t) {
                    display: none;
                }
            }
        }
    }
}