.b-box {
    &--quality {
        @extend %display-flex;
        @extend %align-items-center;
        @extend %justify-content-flex-start;

        flex-grow: 1;

        height: 140px;

        position: relative;

        color: $color_white;
        background: $color_blue;

        &__title {
            margin: 0 0 10px 0;

            font-size: 1.8rem;
            font-weight: 700;

            line-height: 1.3;

            @include gridle_state(m-s) {
                font-size: 1.6rem;
            }

            span {
                position: relative;
                padding-right: 17px;

                &:after {
                    position: absolute;
                    top: 0;
                    right: 0;

                    font-family: $font_primary;
                    font-size: 2.7rem;
                    line-height: 1;

                    content: '+';

                    color: $color_red;

                    @include gridle_state(m-s) {
                        font-size: 2.2rem;
                    }
                }
            }
        }

        &__subtitle {
            margin: 0;

            font-size: 1.2rem;
            font-weight: 700;

            line-height: 1;

            color: $color_red;
        }

        &__ico {
            width: 110px;
            flex-shrink: 0;

            @include gridle_state(m-s) {
                width: 90px;
            }

            .o-ico {
                display: block;

                margin-left: auto;
                margin-right: auto;
            }
        }
    }
}