.l-sec {
    &--head {
        position: absolute;
        top: 50px;
        left: 0;

        z-index: 500;

        width: 100%;

        padding-top: 20px;
        padding-bottom: 20px;

        margin: -20px 0;

        overflow: hidden;

        @include gridle_state(t) {
            position: fixed;
            top: 0;

            padding-bottom: 10px;

            background: $color_white;
        }

        &__box {
            height: 80px;
            margin-left: 323px;
            padding: 0 15px 0 35px;
            background: $color_white;

            @include gridle_state(t) {
                height: 60px;
                margin-left: 160px;
            }
        }

        .l-inner {
            &:before,
            &:after {
                position: absolute;
                top: 0;
                content: '';

                width: 2000px;
                height: 80px;

                background: $color_white;

                @include gridle_state(t) {
                    height: 60px;
                }
            }

            &:before {
                left: -1980px;
            }

            &:after {
                right: -1980px;
            }
        }

        .c-logo--main {
            position: absolute;
            top: -19px;
            left: 50px;

            @include gridle_state(t) {
                top: 5px;
                left: 20px;
                width: 134px;
                height: 60px;
            }
        }

        .c-btn--trigger-nav-main {
            display: none;

            @include gridle_state(t) {
                float: right;
                display: block;
                margin-top: 15px;
                margin-right: -10px;
            }
        }

        &.is-sticky {
            position: fixed;
            top: 0;

            overflow: visible;

            padding-bottom: 0;

            background: $color_white;

            .l-inner {
                &:before,
                &:after {
                    height: 40px;

                    display: none;
                }
            }

            .l-sec--head__box {
                height: 65px;
                margin-left: 190px;
            }

            .c-logo--main {
                top: 5px;
                width: 127px;
                height: 55px;

                @include gridle_state(t) {
                    top: 5px;
                }
            }

            .c-nav--main__list {
                height: 65px;

                @include gridle_state(t) {
                    height: auto;
                }
            }

            .c-nav--main__item .c-label {
                font-size: 1.6rem;
            }
        }        
    }
}

.l-sec {
    &--head {
        &.is-menu-opened {
            .c-nav--main {
                opacity: 1;
                visibility: visible;
            }
        }
    }
}

