.b-box {
    &--hero {
        position: relative;
        height: 900px;
        height: 100vh;

        text-align: center;

        @include gridle_state(t) {
            height: 350px;
        }

        @include gridle_state(m-m) {
            height: 300px;
        }

        &--alt {
            height: 150px;

            @include gridle_state(t) {
                height: 70px;
            }

            .b-box--hero__pic {
                background-size: auto;
            }
        }

        &__pic {
            display: block;
            width: 100%;
            height: 100%;
            
            @extend %helper-bg-size-cover;
            @extend %helper-bg--repeat-no-repeat;
            @extend %helper-bg-position-center-center;
        }

        &__title {
            position: absolute;
            top: 50%;
            left: 0;

            width: 100%;

            margin: 0;

            font-family: $font_primary;
            font-size: 9rem;
            font-weight: 700;
            text-transform: uppercase;

            letter-spacing: 18px;

            line-height: 1;

            color: $color_white;
            text-shadow: 0 0 63px rgba(0, 0, 0, 0.6);

            transform: translateY(-50%);

            @include gridle_state(t) {
                font-size: 5rem;
            }

            @include gridle_state(m-m) {
                font-size: 2.4rem;
                letter-spacing: 10px;
            }

            span {
                display: block;

                margin-top: 10px;

                font-size: 7rem;

                letter-spacing: 15px;

                @include gridle_state(t) {
                    font-size: 3rem;
                }

                @include gridle_state(m-m) {
                    font-size: 2rem;
                }
            }
        }
    }
}