.b-box {
    &--cookies {
        position: fixed;
        left: 0;
        bottom: 0;

        display: none;

        width: 100%;

        padding: 20px 0;

        color: $color_white;
        background: $color_red;

        &.is-active {
            display: block;
        }

        &__main {
            @extend %display-flex;
            @extend %align-items-center;
            @extend %justify-content-flex-end;
            @extend %flex-wrap-wrap;

            a {
                text-decoration: underline;

                color: $color_white;
            }
        }

        &__desc,
        &__btn {
            margin: 0;
        }

        &__btn {
            margin-left: 120px;
        }
    }
}