
/* Colors
========================================================================== */
$color_black:       #000000;
$color_black_2:     #0e0e0e;
$color_white:       #fff;

$color_red:         #c61c2c;
$color_gray:        #f3f6f7;
$color_gray_2:      #6b6b6b;
$color_gray_3:      #262626;
$color_gray_4:      #3b3b3b;
$color_gray_5:      #383636;
$color_gray_6:      #b2b2b2;

$color_blue:      #161517;

$scotch-colors: (
  'black': (
    $color_black
  ),
  'white': (
    $color_white
  ),
  'red': (
    $color_red
  ),
  'gray': (
    $color_gray
  )
);

/* Sizes
========================================================================== */
$size_inner:        1120px + 40px;

$breakpoint_tablet: 1024px;
$breakpoint_tablet_middle: 980px;
$breakpoint_mobile: 767px;
$breakpoint_mobile_middle: 670px;
$breakpoint_mobile_small: 480px;

$breakpoints: (
  t: $breakpoint_tablet,
  t-m: $breakpoint_tablet_middle,
  m: $breakpoint_mobile,
  m-m: $breakpoint_mobile_middle,
  m-s: $breakpoint_mobile_small
);

/* Fonts
========================================================================== */

$font_roboto:     'Roboto';
$font_nexa_bold:  'Nexa Bold';

$font_normal:     $font_roboto, Arial, sans-serif;
$font_primary:    $font_nexa_bold, Arial, sans-serif;
