.o-ico {
    display: block;
}

.o-ico {
    &--time {
        width: 58px;
        height: 57px;

        fill: $color_white;
    }
}

.o-ico {
    &--sound {
        width: 51px;
        height: 51px;

        fill: $color_white;
    }
}

.o-ico {
    &--quarant {
        width: 62px;
        height: 60px;

        fill: $color_white;
    }
}

.o-ico {
    &--quality {
        width: 19px;
        height: 47px;

        fill: $color_white;
    }
}

.o-ico {
    &--temp {
        width: 46px;
        height: 49px;

        fill: $color_white;
    }
}

.o-ico {
    &--newest {
        width: 54px;
        height: 42px;

        fill: $color_white;
    }
}