.c-nav {
    &--top-links {
        &__list {
            @extend %helper-reset-list;
            @extend %display-flex;
            @extend %align-items-center;
            @extend %justify-content-flex-end;
        }

        &__item {
            position: relative;

            margin-left: 45px;

            &:last-child {
                &:before {
                    display: none;
                }
            }

            .c-label {
                font-size: 1.2rem;
                font-weight: 500;
                text-transform: uppercase;

                line-height: 1;
            }

            &:before {
                position: absolute;
                top: 4px;
                right: -25px;

                font-size: 1.2rem;
                font-weight: 500;
                line-height: 1;

                color: $color_red;

                content: '+';
            }
        }
    }
}