.c-btn {
    &--default {
        width: 130px;
        height: 40px;

        font-size: 1.4rem;
        font-weight: 400;
        text-align: center;
        text-transform: uppercase;
        line-height: 36px;

        color: $color_white;
        border: 2px solid $color_white;
        
        &:hover {
            opacity: 0.7;
        }
    }
}