.c-nav {
    &--foot {
        &__row {
            @extend %helper-reset-list;
            @extend %display-flex;
            @extend %align-items-flex-start;
            @extend %justify-content-flex-start;
            @extend %flex-wrap-wrap;

            @include gridle_state(m-s) {
                display: block;
            }
        }

        &__col {
            width: 50%;

            @include gridle_state(m-s) {
                width: auto;
                margin-bottom: 20px;
            }
        }

        &__title {
            margin-bottom: 17px;
            margin-top: 0;
            
            font-size: 2.6rem;
            font-weight: 300;
            text-transform: uppercase;
            line-height: 1;
            letter-spacing: 2px;

            color: $color_white;

            @include gridle_state(t) {
                font-size: 2rem;
            }
        }

        &__list {
            @extend %helper-reset-list;

            &__item {
                @extend %helper-clear-bottom-margin;

                margin-bottom: 15px;

                .c-label {
                    font-size: 1.3rem;
                    font-weight: 400;
                    text-transform: uppercase;

                    color: $color_white;

                    @include gridle_state(t) {
                        font-size: 1.4rem;
                    }

                    &:hover {
                        color: $color_gray_3;
                    }
                }
            }
        }
    }
}