.c-carousel {
    &--hero {
        position: relative;

        .slick-dots {
            position: absolute;
            bottom: 34px;
            left: 0;

            width: 100%;

            margin: 0;
            padding: 0;
            list-style: none;

            text-align: center;

            @include gridle_state(t) {
                bottom: 20px;
            }

            a {
                display: block;
                position: relative;

                width: 19px;
                height: 19px;

                text-indent: -777em;

                cursor: pointer;

                @include gridle_state(t) {
                    width: 14px;
                    height: 14px;
                }

                &:before,
                &:after {
                    position: absolute;
                    top: 0;
                    left: 0;
                    content: '';
                    width: 100%;
                    height: 100%;

                    background: url("../img/default/ico-plus-1-white.png") no-repeat;
                    background-size: cover;
                }
            }

            li {
                display: inline-block;
                
                margin: 0 8px;

                vertical-align: middle;

                &:hover,
                &.slick-active {
                    a {
                        &:after {
                            background-image: url("../img/default/ico-plus-1-red.png");
                        }
                    }
                }
            }
        }
    }
}