.b-box {
    &--house {
        display: block;
        overflow: hidden;
        position: relative;

        box-shadow: 0 0 50px rgba(0, 0, 0, 0.2);

        &__bg {
            @extend %helper-bg-size-cover;
            @extend %helper-bg--repeat-no-repeat;
            @extend %helper-bg-position-center-center;

            display: block;

            width: 100%;
            height: 330px;

            transition: 200ms ease-in-out;

            @include gridle_state(m-m) {
                height: 50vw;
            }
        }

        &__title,
        &__btn {
            position: absolute;
            margin: 0;

            width: 100%;

            transition: 200ms ease-in-out;
        }

        &__title {
            top: 50px;
            left: 0;

            font-size: 3.4rem;
            font-weight: 300;
            
            text-align: center;
            text-transform: uppercase;

            line-height: 1;
            letter-spacing: 16px;

            @include gridle_state(m-m) {
                font-size: 2.4rem;
            }

            &--alt {
                top: 50%;

                font-size: 3rem;
                font-weight: 400;

                letter-spacing: 14px;

                color: $color_white;

                @include gridle_state(m-m) {
                    font-size: 1.4rem;
                    letter-spacing: 10px;
                }

                span {
                    position: relative;
                    left: -4px;
                    display: block;

                    margin-bottom: 10px;

                    font-size: 3.4rem;
                    font-weight: 300;

                    letter-spacing: 3px;

                    @include gridle_state(m-m) {
                        left: -2px;
                        font-size: 1.8rem;
                    }
                }

                transform: translateY(-50%);
            }
        }

        &__btn {
            bottom: 50px;
            left: 0;

            margin: 0;

            text-align: center;
        }

        &:hover {
            .b-box--house__title {
                transform: translateY(10px);

                &--alt {
                    transform: translateY(-50%) scale(1.1);
                }
            }

            .b-box--house__btn {
                transform: translateY(-10px);
            }

            .b-box--house__bg {
                transform: scale(1.1);
            }
        }
    }
}