
/* Text aligned
   ========================================================================== */

.u-text-left {
    text-align: left;
}

.u-text-center {
    text-align: center;

    .c-btn {
        margin-left: auto;
        margin-right: auto;
    }
}

.u-text-right {
    text-align: right;
}

.u-pull-right {
    float: right;
}

.u-pull-left {
    float: left;
}

.u-text-uppercase {
    text-transform: uppercase;
}

.u-text-underline {
    text-decoration: underline;
}

.u-hidden {
    display: none;
}

.u-show {
    display: block;
}

 /* Backgrounds
    ========================================================================== */

@each $pallet, $value in $scotch-colors {
    @each $name in $value {
        $i: index($value, $name);

        .u-bg-#{$pallet}-#{$i} {
            background: #{$name};
        }
    }
}

.u-bg-none {
    background: none;
}

/**
 * Offsets for the grid
 *
 * Offsets are used in the Grid
 *
 * Variants
 * - u-offset-row-{number from 0 to 10}
 * - u-offset-row-{number from 0 to 10}@{state}
 *
 * @section Offsets rows
 * @sectionof Modifiers
 * @page Base
 * @example
 * <div class="row u-offset-row-3">
 *     <div class="gr gr-6 gr-12@m-m">
 *         <span class="u-bg-gray-2" style="display: block;">Text 1</span>
 *     </div>
 *     <div class="gr gr-6 gr-12@m-m">
 *         <span class="u-bg-gray-2" style="display: block;">Text 2</span>
 *     </div>
 * </div>
 */
.u-offset-row-0 {
    margin-left: -(2px);
    margin-right: -(2px);

    > .gr {
        padding-left: 2px;
        padding-right: 2px;
    }
}

@for $i from 1 through 10 {
    [class*="gr-"] > [class^="row"].u-offset-row-#{$i},
    .u-offset-row-#{$i} {
        margin-left: -(5px * $i);
        margin-right: -(5px * $i);

        > .gr {
            padding-left: 5px * $i;
            padding-right: 5px * $i;
        }
    }
}

@each $name, $value in $breakpoints {
    @include gridle_state($name) {
        .u-offset-row-0\@#{$name} {
            margin-left: -(2px);
            margin-right: -(2px);

            > .gr {
                padding-left: 2px;
                padding-right: 2px;
            }
        }
    }
}

@each $name, $value in $breakpoints {
    @include gridle_state($name) {
        @for $i from 1 through 10 {
            [class*="gr-"] > [class^="row"].u-offset-row-#{$i}\@#{$name},
            .u-offset-row-#{$i}\@#{$name} {
                margin-left: -(5px * $i);
                margin-right: -(5px * $i);

                > .gr {
                    padding-left: 5px * $i;
                    padding-right: 5px * $i;
                }
            }
        }
    }
}

/**
 * Spacings cols for the grid
 *
 * Spacings cols are used in the Grid
 *
 * Variants
 * - u-space-col-{number from 0 to 10}
 * - u-space-col-{number from 0 to 10}@{state}
 *
 * @section Spacings cols
 * @sectionof Modifiers
 * @page Base
 * @example
 * <div class="row u-offset-row-3 u-space-col-6">
 *     <div class="gr gr-6 gr-12@m-m">
 *         <span class="u-bg-gray-2" style="display: block;">Text 1</span>
 *     </div>
 *     <div class="gr gr-6 gr-12@m-m">
 *         <span class="u-bg-gray-2" style="display: block;">Text 2</span>
 *     </div>
 *     <div class="gr gr-6 gr-12@m-m">
 *         <span class="u-bg-gray-2" style="display: block;">Text 3</span>
 *     </div>
 *     <div class="gr gr-6 gr-12@m-m">
 *         <span class="u-bg-gray-2" style="display: block;">Text 4</span>
 *     </div>
 * </div>
 */
.u-space-col-0 {
    > .gr {
        margin-top: 0;
    }
}

@each $name, $value in $breakpoints {
    @include gridle_state($name) {
        .u-space-col-0\@#{$name} {
            > .gr {
                margin-top: 0;
            }
        }
    }
}

@for $i from 1 through 5 {
    .u-space-col-#{$i} {
        margin-top: -(5px * $i);

        > .gr {
            margin-top: 5px * $i;

            &.u-space-top-0 {
                margin-top: 0;
            }
        }
    }
}

@each $name, $value in $breakpoints {
    @include gridle_state($name) {
        @for $i from 1 through 5 {
            .u-space-col-#{$i}\@#{$name} {
                margin-top: -(5px * $i);
                
                > .gr {
                    margin-top: 5px * $i;

                    &.u-space-top-0 {
                        margin-top: 0;
                    }
                }
            }
        }
    }
}